@import url("https://fonts.googleapis.com/css2?family=Cardo:wght@700&family=Montserrat:wght@300;500;700&family=Raleway:wght@300;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cardo:wght@700&family=Commissioner:wght@700&family=Montserrat:wght@300;500;700&family=Raleway:wght@300;500;700&display=swap");

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  width: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
header {
  width: 100%;
  height: 80px;
  background-color: rgba(33, 29, 31, 0.6);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
}
header .container {
  width: 1140px;
  height: 40px;
  margin: auto;
  padding-top: 24px;
  background-color: none;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  position: relative;
}
header .container .ulLeft {
  list-style: none;
  padding-left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
header .container .ulLeft li {
  width: 100px;
  height: 100%;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  transition: 0.5s;
  cursor: pointer;
}
header .container .ulLeft li:hover {
  color: #ec8b84;
}
header .container .ulLeft li:nth-child(1) {
  margin-bottom: -5px;
  width: 18px;
  margin-right: 60px;
}
header .container .ulLeft .active {
  color: #e61919;
}
header .container h1 {
  font-family: "Commissioner";
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 44px;
  color: #ffffff;
  margin: 0 150px;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  top: -5px;
  cursor: pointer;
}

header .container span {
  color: #fff;
  cursor: pointer;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  transition: 0.5s;
}
header .container span .line {
  position: relative;
  top: 3px;
  transition: 0.5s;
}
header .container span:hover {
  color: #e0bea2;
}
header .container span:hover .line {
  transform: rotate(180deg);
}
header .container span .ulRight {
  position: absolute;
  top: 70px;
  right: 365px;
}
header .container span .ulRight li {
  list-style: none;
  width: 80px;
  height: 20px;
  position: relative;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  color: #fff;
  background-color: #212121;
  border-bottom: 0.5px solid #fff;
  left: 60px;
  top: -20px;
  cursor: pointer;
  transition: 0.5s;
  text-transform: uppercase;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
}
header .container .ulRight li:hover {
  color: #e0bea2;
}
header .container .account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  padding-left: 0;
  list-style: none;
  margin-left: auto;
  width: 140px;
}
header .container .account li {
  width: 20px;
  font-size: 20px;
  transition: 0.5s;
  cursor: pointer;
}
header .container .account li:hover {
  color: #e0bea2;
}
header .container .account .active {
  color: #e0bea2;
}
.border {
  width: 100%;
  display: flex;
  text-align: center;
}
.borderBlock1,
.borderBlock2,
.borderBlock3 {
  width: 34%;
  height: 900px;
}
.borderBlock1 {
  background: linear-gradient(180deg, rgba(61, 61, 61, 0.5) 0%, rgba(61, 61, 61, 0) 100%),
    url(img/1.jfif);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  background-size: cover;
  background-position: center center;
}
.borderBlock2 {
  background: linear-gradient(180deg, rgba(61, 61, 61, 0.5) 0%, rgba(61, 61, 61, 0) 100%),
    url(img/2.jfif);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  background-size: cover;
  background-position: center center;
}
.borderBlock3 {
  background: linear-gradient(180deg, rgba(61, 61, 61, 0.5) 0%, rgba(61, 61, 61, 0) 100%),
    url(img/3.jfif);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  background-size: cover;
  background-position: center center;
}
.border .borderBlock2 .borderInfo {
  margin-top: 460px;
}
.border .borderBlock2 .borderInfo h2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 46px;
  line-height: 54px;
  text-align: center;
  color: #ffffff;
  opacity: 0.85;
  margin-bottom: 20px;
  transform: scaleX(-1);
}
.border .borderBlock2 .borderInfo hr {
  width: 120px;
  height: 1px;
  margin-bottom: 20px;
}
.border .borderBlock2 .borderInfo span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  display: block;
  transform: scaleX(-1);
  cursor: pointer;
  transition: 0.5s;
}
.border .borderBlock2 span:hover {
  color: #e61919;
}
.border .borderBlock2 span .scroll {
  font-size: 11px;
  position: relative;
  top: -1px;
  transition: 0.5s;
}
.border .borderBlock2 span:hover .scroll {
  color: #e61919;
  transform: rotate(270deg);
}
.sort {
  width: 1140px;
  min-height: 542px;
  margin: 200px auto;
}
.categoriesH2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 36px;
  line-height: 42px;
  color: #252525;
  margin: 0 0 80px;
}
.sort .ob .categories {
  width: 280px;
  height: 390px;
  transition: 0.5s;
}
.sort .ob .categories:hover {
  -webkit-box-shadow: 0px -1px 4px 3px #e0bea2;
  -moz-box-shadow: 0px -1px 4px 3px #e0bea2;
  box-shadow: 0px -1px 4px 3px #e0bea2;
}
.sort .ob .categories img {
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.sort .ob {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
.sort .ob .categories .fonSpan {
  position: relative;
  top: -42px;
  width: 100%;
  height: 40px;
  background: rgba(224, 190, 162, 0.75);
  backdrop-filter: blur(3px);
}
.sort .ob .categories .fonSpan span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #ffffff;
  position: relative;
  top: 8px;
  cursor: pointer;
  transition: 0.5s;
}
.sort .ob .categories .fonSpan span:hover {
  color: #e61919;
}
.info {
  width: 600px;
  height: 270px;
  margin: auto;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
.info h2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 36px;
  line-height: 42px;
  text-align: center;
  margin: 0 0 60px;
  color: #252525;
}
.info form input {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 200;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: rgba(37, 37, 37, 0.5);
  width: 594px;
  height: 46px;
  border: 0.5px solid #252525;
  transition: 0.5s;
}
.info form button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  width: 600px;
  height: 50px;
  margin: 20px 0;

  background: #e0bea2;
  border: none;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  cursor: pointer;
  color: #ffffff;
}
.info form button:hover {
  background: #cca88a;
}
.info span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 200;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #252525;
}
footer {
  margin: 0 auto 50px;
  width: 1140px;
  min-height: 120px;
  display: flex;
  justify-content: space-between;
}
footer .fooBlock {
  width: 250px;
  height: 100%;
}
footer .fooBlock h2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 23px;
  color: #252525;
}
footer .fooBlock ul {
  list-style: none;
  padding-left: 0;
  margin-left: 1px;
}
footer .fooBlock ul li {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 200;
  font-size: 16px;
  line-height: 19px;
  color: #252525;
  margin: 10px 0;
  width: 100%;
  transition: 0.5s;
  cursor: pointer;
}
footer .fooBlock ul li:hover {
  color: #e0bea2;
}
footer .fooBlock ul .active {
  color: #e61919;
}

@media screen and (max-width: 1200px) {
  header {
    display: flex;
    justify-content: center;
  }
  header .container {
    width: 1000px;
    margin: auto;
  }
  header .container {
    margin: 0;
  }
  header .container h1 {
    margin: 0 70px;
  }
  .borderBlock1,
  .borderBlock2,
  .borderBlock3 {
    height: 600px;
    width: 34%;
  }
  .borderBlock2 .borderInfo h2 {
    font-size: 36px;
    margin-top: -200px;
  }
  .borderBlock2 .borderInfo span {
    font-size: 12px;
  }
  .sort {
    width: 1000px;
  }
  .sort .ob .categories {
    width: 240px;
    height: 340px;
  }
  footer {
    width: 1000px;
  }
}
@media screen and (max-width: 1140px) {
  .border {
    width: 100%;
  }
  .border .borderBlock2 .borderInfo h2 {
    font-size: 36px;
  }
  .border .borderBlock2 .borderInfo span {
    font-size: 14px;
  }
  /*.borderBlock1,
  .borderBlock3 {
    display: none;
  }*/
  .borderBlock2 {
    min-width: 34%;
    height: 600px;
  }
  .borderBlock2 .borderInfo {
    position: relative;
  }

  header {
    display: flex;
    justify-content: center;
  }
  header .container {
    width: 800px;
    margin: auto;
  }
  header .container h1 {
    margin: 0 80px;
  }
  header .container .ulLeft li:nth-child(1) {
    margin-right: 30px;
  }
  header .container .ulLeft li {
    width: 70px;
    font-size: 14px;
  }
  header .container .ulRight {
    margin-left: 50px;
  }
  header .container .ulRight li {
    font-size: 14px;
  }
  .sort {
    width: 800px;
    margin: 100px auto 0;
    min-height: 450px;
  }
  .sort .ob .categories {
    width: 190px;
    height: 260px;
  }
  .categoriesH2 {
    margin-bottom: 50px;
  }
  footer {
    width: 800px;
    margin: auto;
  }
  footer .fooBlock {
    width: 200px;
  }
  header .container .account li {
    font-size: 18px;
  }
}

@media screen and (max-width: 1000px) {
  .border {
    width: 100%;
  }
  .borderBlock1,
  .borderBlock2,
  .borderBlock3 {
    width: 34%;
  }
  .borderBlock2 .borderInfo h2 {
    font-size: 24px;
  }
  .borderBlock2 .borderInfo span {
    font-size: 12px;
  }
  header .container {
    width: 800px;
    margin: auto;
  }
}
@media screen and (max-width: 820px) {
  header .container {
    width: 600px;
  }
  header .container .ulLeft li:nth-child(2),
  header .container .ulLeft li:nth-child(4) {
    display: none;
  }
  header .container h1 {
    width: 200px;
  }
  header .container span {
    display: none;
  }
  header .container .account {
    width: 50px;
  }
  header .container .account li:nth-child(3),
  header .container .account li:nth-child(4) {
    display: none;
  }
  .border .borderBlock2 .borderInfo {
    margin-top: 400px;
  }
  .border .borderBlock2 .borderInfo h2 {
    margin-bottom: 0;
    font-size: 24px;
  }
  .border .borderBlock2 .borderInfo span {
    font-size: 12px;
  }
  .sort {
    width: 600px;
    margin: 100px auto 0;
    min-height: 450px;
  }
  .sort .ob .categories {
    width: 145px;
    height: 200px;
  }
  .categoriesH2 {
    font-size: 22px;
    margin-bottom: 50px;
  }
  .info h2 {
    font-size: 30px;
  }
  .info form input {
    width: 394px;
  }
  .info form button {
    width: 400px;
  }
  .info span {
    font-size: 12px;
  }
  footer {
    width: 600px;
    margin: auto;
  }
  footer .fooBlock {
    width: 150px;
  }
  footer .fooBlock h2 {
    font-size: 16px;
  }
  footer .fooBlock ul li {
    font-size: 12px;
  }
}
@media screen and (max-width: 620px) {
  header .container {
    width: 400px;
  }
  header .container h1 {
    margin: 0;
    width: 155px;
  }
  .border .borderBlock1,
  .border .borderBlock3 {
    display: none;
  }
  .border .borderBlock2 {
    height: 600px;
    width: 100%;
  }
  .border .borderBlock2 .borderInfo h2 {
    font-size: 42px;
  }
  .border .borderBlock2 .borderInfo span {
    font-size: 20px;
  }

  .sort {
    width: 400px;
    margin: 80px auto 0;
    min-height: 450px;
  }
  .sort .ob .categories {
    width: 190px;
    height: 260px;
    margin-bottom: 20px;
  }
  .categoriesH2 {
    font-size: 26px;
    margin-bottom: 50px;
  }

  .info {
    margin-top: 50px;
    width: 400px;
  }
  .info h2 {
    font-size: 28px;
    margin-bottom: 30px;
  }
  .info form input {
    width: 294px;
  }
  .info form button {
    width: 300px;
  }
  .info span {
    font-size: 14px;
    width: 400px;
  }

  footer {
    width: 410px;
    margin: auto;
  }
  footer .fooBlock {
    width: 100px;
  }
  footer .fooBlock h2 {
    font-size: 14px;
  }
  footer .fooBlock ul li {
    font-size: 10px;
  }
}
@media screen and (max-width: 430px) {
  header .container {
    width: 300px;
  }
  header .container h1 {
    width: 200px;
  }
  header .container .ulLeft li:nth-child(3) {
    display: none;
  }
  header .container .account li:nth-child(1) {
    display: none;
  }
  header .container .account {
    width: 20px;
  }

  .border .borderBlock2 {
    height: 600px;
    width: 100%;
  }
  .border .borderBlock2 .borderInfo h2 {
    font-size: 38px;
  }
  .border .borderBlock2 .borderInfo span {
    font-size: 18px;
  }

  .sort {
    width: 300px;
    margin: 50px auto 0;
    min-height: 450px;
  }
  .sort .ob .categories {
    width: 140px;
    height: 200px;
    margin-bottom: 20px;
  }
  .categoriesH2 {
    font-size: 26px;
    margin-bottom: 20px;
  }

  .info {
    margin-top: 30px;
    width: 300px;
  }
  .info h2 {
    font-size: 22px;
    margin-bottom: 20px;
  }
  .info form input {
    width: 194px;
  }
  .info form button {
    width: 200px;
  }
  .info span {
    font-size: 12px;
    width: 400px;
  }

  footer {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-wrap: wrap;
  }
  footer .fooBlock {
    width: 100%;
    height: 70px;
  }
  footer .fooBlock h2 {
    font-size: 18px;
  }
  footer .fooBlock ul li {
    display: none;
  }
}

@media screen and (max-width: 320px) {
  header .container {
    width: 200px;
  }
  header .container h1 {
    width: 100px;
  }

  .border .borderBlock2 {
    height: 600px;
    width: 100%;
  }
  .border .borderBlock2 .borderInfo h2 {
    font-size: 32px;
  }
  .border .borderBlock2 .borderInfo span {
    font-size: 14px;
  }

  .sort {
    width: 200px;
    margin: 50px auto 0;
    min-height: 450px;
  }
  .sort .ob .categories {
    width: 190px;
    height: 280px;
    margin-bottom: 20px;
  }
  .categoriesH2 {
    font-size: 26px;
    margin-bottom: 20px;
  }

  .info {
    margin-top: 30px;
    width: 200px;
  }
  .info h2 {
    font-size: 18px;
    margin-bottom: 20px;
    line-height: 25px;
  }
  .info form input {
    width: 144px;
  }
  .info form button {
    width: 150px;
    height: 40px;
  }
  .info span {
    font-size: 12px;
    width: 200px;
  }

  footer .fooBlock h2 {
    font-size: 16px;
  }
}
